<template>
  <div class="songdetail">
    <div class="part1">
      <div class="backgroud">
        <div class="picture"></div>
      </div>
      <div class="detail">
        <div class="image">
          <img :src="this.$route.params.item.picUrl" alt srcset>
        </div>
        <div class="text">
          <h4>{{this.$route.params.item.name}}</h4>
          <p>{{this.$route.params.item.playCount}}</p>
        </div>
      </div>
    </div>
    <div class="synopsisa">
      标签：
      <p v-for="(item, index) in tags" :key="index">{{item}}</p>
      <pre>简介：{{description}}</pre>
    </div>
    <h3>歌曲列表</h3>
    <SongList2 v-for="(item, index) in tracks" :key="index" 
    :item="item"  :index="index" :tracks="tracks"></SongList2>
  </div>
</template>
<style lang="less" scoped>
.songdetail {
  width: 100%;
  .part1 {
    overflow: hidden;
    height: 150px;
    .backgroud {
      overflow: hidden;
      width: 100%;
      height: 150px;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;

      filter: blur(20px);

      .picture {
        width: 100%;
        height: 150px;
        background: url("../image/backgroud.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        transform: scale(1.5);
      }
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-color: rgba(0, 0, 0, 0.25);
      }
    }
    .detail {
      display: flex;
      justify-content: space-around;
      align-items: center;
      width: 100%;
      height: 150px;
      .image {
        width: 114px;
        height: 114px;
        z-index: 12;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .text {
        z-index: 12;
        width: 120px;
        h4 {
          padding-top: 1px;
          margin: 0;
          font-weight: 400;
          font-size: 17px;
          line-height: 1.3;
          color: #fefefe;
          height: 44px;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }

  .synopsisa {
    background: white;
    line-height: 19px;
    color: #666;
    p {
      display: inline-block;
      margin-right: 10px;
      padding: 1px 8px;
      font-size: 12px;
      border: 1px solid #ebebeb;
      border-radius: 10px;
    }
    pre {
      display: block;

      text-overflow: ellipsis;
      overflow: hidden;
      
      height: 81px;
      padding-left: 15px;
      line-height: 15px;
      margin: 0;
    }
  }
  h3 {
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }
}
</style>
<script>
import SongList2 from "@/components/SongList2.vue";
export default {
  components:{
    SongList2
  },
  data() {
    return {
      playlist: null,
      privileges: null,
      tags:null,
      description:null,
      tracks:null
    };
  },
 methods: {
  
 },
  created() {
    // console.log(this.$route.params.item);
    let that = this;
    axios
      .get("/playlist/detail", {
        params: {
          id: this.$route.params.item.id
        }
      })
      .then(function(response) {
        // console.log(response);
        that.playlist = response.data.playlist;
        that.privileges = response.data.privileges;
        that.tags=response.data.playlist.tags;
        that.description=response.data.playlist.description;
        that.tracks=response.data.playlist.tracks;
        // console.log(that.playlist);
        // console.log(that.privileges);
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>